a {
  color: inherit;
  text-decoration: none;
}

li {
  text-decoration: none;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

input, button {
  border: none;
  outline: none;
}

